import React, { Component } from 'react';
import axios from 'axios';
import TinyQueue from 'tinyqueue';
import './App.css';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import ReactTooltip from 'react-tooltip'
class App extends Component {
    constructor(props) {
        super(props);
        this.state = { is_hidden: 'block', seconds:[] , data: [] , axies_tuples : [], pq: [] , marketURL : "https://axieinfinity.com/api/axies?sale=1&sorting=lowest_price&offset="};
    }

    tick() {
        this.setState(state => ({
            seconds: state.seconds.map(function(value){return value -1})
        }));
    }

    componentDidMount() {
        this.getMarketAxies(0)
        this.interval = setInterval(() => this.tick(), 1000);
    }

    getMarketAxies(offset){
        let url = this.state.marketURL + offset;
        axios.get(url)
            .then(res => {
                for (let i in res.data.axies) {
                    if (res.data.axies[i].stage < 3) continue;
                    this.state.axies_tuples.push({id: res.data.axies[i].id,timeLeft: res.data.axies[i].auction.timeLeft})
                }
                if (offset < 100) {
                    this.getMarketAxies(offset + 12);
                }
                else{
                    this.populatePriorityQ()
                }
            })
    }
    populatePriorityQ(){
        this.setState({pq : new TinyQueue(this.state.axies_tuples, function (a, b) {
          return a.timeLeft - b.timeLeft;})})
        let tup = this.state.pq.pop()
        while(tup['timeLeft'] == 0 ){
            tup = this.state.pq.pop()
        }
        tup = this.state.pq.pop()
        while (tup != undefined){
            let url = "https://axieinfinity.com/api/axies/" + tup['id']
            this.state.seconds.push(tup['timeLeft'])
            axios.get(url)
                .then(res => {
                    this.state.data.push({timeLeft: this.state.seconds[this.state.seconds.length-1],id: res.data.id, owner: res.data.owner, class: res.data.class, exp: res.data.exp, price: res.data.auction.buyNowPrice/1e18, stats: res.data.stats})
                })
            tup = this.state.pq.pop()
        }
        this.setState({is_hidden: 'none'})
    }

    sec2time(timeInSeconds) {
        var pad = function(num, size) { return ('000' + num).slice(size * -1); },
            time = parseFloat(timeInSeconds).toFixed(3),
            hours = Math.floor(time / 60 / 60),
            minutes = Math.floor(time / 60) % 60,
            seconds = Math.floor(time - minutes * 60)

        return pad(hours, hours.toString().length) + ':' + pad(minutes, 2) + ':' + pad(seconds, 2) ;
    }

    componentWillUnmount() {
        clearInterval(this.interval);
    }
    getStyle(i){
        return {
            color: i<36000 ? 'red' : 'green'
        }
    }
    render() {
        return (
            <div >

                <text style={{display: 'flex',  justifyContent:'center', alignItems:'center', height: '10vh', fontSize: '25px',fontFamily: "Comic Sans MS",color:"white"}}> Jaewoo's Realtime Axie Auction</text>
                <div>
                <a data-tip="Welcome to the Realtime Axie Auction Tool for Axie Infinity! This project is inspired by the original Axie market.
                One potential improvement of the market was to show the sale countdowns for each Axie. Mimicing the style of a real auction has certain benefits to the Axie market. Dynamic time information is important for buyers because they can plan out bids for a particular Axie. An auction style view also leads to active participation from the users in the market because countdowns call for immediate action. In summary, this tool hopefully allows users to not miss any Axies by mistake, or gives users the chance to outbid other users at the last moment." style={{marginLeft:"20vh",color:"white"}}> hover over me!  d(`･∀･)b    </a>
                <ReactTooltip place="right" type="dark" effect="solid" style={{marginLeft:"20px"}}/>
                </div>
                    <div style={{display: 'flex',  justifyContent:'center', alignItems:'center', overflow: 'auto',margin: 'auto'}}>
                    <img  src= "https://axieinfinity.com/assets/images/bdb2b2a03ae24ab6860ceae5ceea3fe6.png" style = {{margin: 'auto'}} />
                    </div>

                <div style={{height:'20px'}}></div>
                <div className="col-sm-6" style={{display: 'block',  justifyContent:'center', alignItems:'center',height: '70vh', width: '120vh', overflow: 'auto',margin: 'auto'}} >
                    <Paper>
                        <Table>
                            <TableHead>
                                <TableRow>
                                    <TableCell>Axie ID</TableCell>
                                    <TableCell>Time Left</TableCell>
                                    <TableCell>Price</TableCell>
                                    <TableCell>Owner</TableCell>
                                    <TableCell>Class</TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {
                                    this.state.data.map((user,i) =>
                                    <TableRow key={i}>
                                        <TableCell >{user.id}</TableCell>
                                        <TableCell style={this.getStyle(this.state.seconds[i])}>{this.sec2time(this.state.seconds[i])}</TableCell>
                                        <TableCell >{user.price}</TableCell>
                                        <TableCell >{user.owner}</TableCell>
                                        <TableCell >{user.class}</TableCell>
                                    </TableRow>
                                )}
                            </TableBody>
                        </Table>
                    </Paper>
                    <div style={{height:'40px'}}></div>
                    <div className="loader" style={{display: this.state.is_hidden,  justifyContent:'center', alignItems:'center', overflow: 'auto',margin: 'auto'}}></div>
                </div>
            </div>
        );
    }
}

export default App;
